






.fade-transition {
    -webkit-transition: opacity 0.2s ease;
    transition: opacity 0.2s ease;
}
.fade-enter, .fade-leave {
    opacity: 0;
}
@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}



